import React from "react";
import { useDrag, DragPreviewImage } from "react-dnd";
import Images from "./Images/Images.js";

import PropTypes from "prop-types";
import { connect } from "react-redux";
import { toggleAvailableMoves } from "../actions/chessActions.js";

function BoardPiece({
  imagestyle,
  pieceimageindex,
  piece,
  thisX,
  thisY,
  thisBoardState,
  toggleAvailableMoves
}) {
  const [{ isDragging }, drag, preview] = useDrag({
    item: { type: piece.chessType },
    begin: () => {
      toggleAvailableMoves(piece, thisBoardState, thisX, thisY);
    },
    end: () => {
      toggleAvailableMoves(null, thisBoardState, null, null);
    },
    collect: monitor => ({
      isDragging: monitor.isDragging()
    })
  });
  return (
    <div>
      <DragPreviewImage connect={preview} src={Images[pieceimageindex]} />
      <img
        ref={drag}
        style={{
          ...imagestyle,
          opacity: isDragging ? 0.5 : 1
        }}
        src={Images[pieceimageindex]}
        alt={piece.chessType}
      />
    </div>
  );
}

BoardPiece.propTypes = {
  toggleAvailableMoves: PropTypes.func.isRequired
};

export default connect(
  null,
  { toggleAvailableMoves }
)(BoardPiece);
