import {
  UPDATE_BOARD,
  UPDATE_WHITE_PIECES,
  UPDATE_BLACK_PIECES,
  TOGGLE_AVAILABLE_MOVES
} from "./types.js";
import "../styles/style.css";

import PieceRules from "../components/PieceRules.js";

export const updateBoard = newBoardState => dispatch => {
  console.log(newBoardState);
  dispatch({
    type: UPDATE_BOARD,
    payload: newBoardState
  });
};

export const updateWhitePieces = () => dispatch => {
  dispatch({
    type: UPDATE_WHITE_PIECES
  });
};

export const updateBlackPieces = () => dispatch => {
  dispatch({
    type: UPDATE_BLACK_PIECES
  });
};

//FOR TOGGLE AVAILABLE MOVES

export const toggleAvailableMoves = (
  clickedPiece,
  currentBoardState,
  thisPieceX,
  thisPieceY
) => dispatch => {
  let availableMoves = [];
  let clickedPiecePosition = { piece: null, position: [] };
  if (thisPieceX != null) {
    clickedPiecePosition = {
      piece: clickedPiece,
      position: [thisPieceX, thisPieceY]
    };
  }
  if (clickedPiece) {
    switch (clickedPiece.chessType) {
      case "pawn":
        availableMoves.push(
          PieceRules.PawnRules(
            thisPieceX,
            thisPieceY,
            clickedPiece.color,
            currentBoardState
          )
        );
        break;
      case "rook":
        availableMoves.push(
          PieceRules.RookRules(
            thisPieceX,
            thisPieceY,
            clickedPiece.color,
            currentBoardState
          )
        );
        break;
      case "knight":
        availableMoves.push(
          PieceRules.KnightRules(
            thisPieceX,
            thisPieceY,
            clickedPiece.color,
            currentBoardState
          )
        );
        break;
      case "bishop":
        availableMoves.push(
          PieceRules.BishopRules(
            thisPieceX,
            thisPieceY,
            clickedPiece.color,
            currentBoardState
          )
        );
        break;
      case "queen":
        availableMoves.push(
          PieceRules.QueenRules(
            thisPieceX,
            thisPieceY,
            clickedPiece.color,
            currentBoardState
          )
        );
        break;
      case "king":
        availableMoves.push(
          PieceRules.KingRules(
            thisPieceX,
            thisPieceY,
            clickedPiece.color,
            currentBoardState
          )
        );
        break;
      default:
        break;
    }
  }
  dispatch({
    type: TOGGLE_AVAILABLE_MOVES,
    payload: availableMoves,
    clickedPiecePosition: clickedPiecePosition
  });
};
