import React, { Component } from "react";
import "../styles/style.css";
import { Piece, TYPES } from "./Piece.js";
import { DndProvider } from "react-dnd";
import HTML5Backend from "react-dnd-html5-backend";
//import Images from "./Images/Images.js";

import SquarePiece from "./SquarePiece.js";

import PropTypes from "prop-types";
import { connect } from "react-redux";
import {
  updateBoard,
  updateBlackPieces,
  updateWhitePieces,
  toggleAvailableMoves
} from "../actions/chessActions";

export const boardWidthHeight = 8;
export const widthHeight = 100;

class Board extends Component {
  constructor(props) {
    super(props);
    this.componentWillMount = this.componentWillMount.bind(this);
    this.handleBoardRender = this.handleBoardRender.bind(this);
    this.checkForWhichPiece = this.checkForWhichPiece.bind(this);
  }

  componentWillMount() {
    let initialBoardState = this.props.boardState;
    for (let y = 0; y < boardWidthHeight; y++) {
      for (let x = 0; x < boardWidthHeight; x++) {
        let color = y > 2 ? "white" : "black";
        let newPieceObject = this.checkForWhichPiece(
          this.props.boardState[y][x],
          color
        );
        initialBoardState[y][x] = newPieceObject.newPiece;
      }
    }
    this.props.updateBoard(initialBoardState);
  }

  handleBoardRender() {
    // render the board
    let newBackgroundBoard = [];
    let currentBoardState = this.props.boardState;
    let a = 0;
    let b = 0;
    let key = 0;
    for (let y = 0; y < boardWidthHeight; y++) {
      for (let x = 0; x < boardWidthHeight; x++) {
        a = a > 1 ? 0 : a;
        b = b > 1 ? 0 : b;
        let color = this.props.boardState[y][x].color;
        let backcolor = b === a ? "lightgrey" : "grey";
        let top = y * widthHeight;
        let left = x * widthHeight;
        let newPieceObject = this.checkForWhichPiece(
          this.props.boardState[y][x].chessType,
          color
        );

        let style = {
          position: "absolute",
          background: backcolor,
          display: "inline-block",
          color: color,
          top: top,
          left: left,
          width: widthHeight,
          height: widthHeight
        };

        let imageStyle = {
          height: widthHeight,
          width: widthHeight
        };

        const squarePiece = (
          <SquarePiece
            key={key}
            divstyle={style}
            imagestyle={imageStyle}
            piece={newPieceObject.newPiece}
            pieceimageindex={newPieceObject.pieceImage}
            thisX={x}
            thisY={y}
            thisBoardState={currentBoardState}
            availableMoves={this.props.toggleAvailableMovesBoard}
            clickedPiecePosition={this.props.clickedPiecePosition}
          />
        );
        newBackgroundBoard.push(squarePiece);
        currentBoardState[y][x] = newPieceObject.newPiece;
        a++;
        key++;
      }
      b++;
    }
    this.props.updateBoard(currentBoardState);
    return newBackgroundBoard;
  }

  checkForWhichPiece(whichPiece, color) {
    let newPiece;
    let pieceImage;
    switch (whichPiece) {
      case "pawn":
        newPiece = new Piece(TYPES.PAWN, color);
        pieceImage = color === "white" ? 5 + 6 : 5;
        break;
      case "rook":
        newPiece = new Piece(TYPES.ROOK, color);
        pieceImage = color === "white" ? 2 + 6 : 2;
        break;
      case "knight":
        newPiece = new Piece(TYPES.KNIGHT, color);
        pieceImage = color === "white" ? 4 + 6 : 4;
        break;
      case "bishop":
        newPiece = new Piece(TYPES.BISHOP, color);
        pieceImage = color === "white" ? 3 + 6 : 3;
        break;
      case "queen":
        newPiece = new Piece(TYPES.QUEEN, color);
        pieceImage = color === "white" ? 1 + 6 : 1;
        break;
      case "king":
        newPiece = new Piece(TYPES.KING, color);
        pieceImage = color === "white" ? 0 + 6 : 0;
        break;
      default:
        newPiece = new Piece(TYPES.EMPTY);
        break;
    }
    return { newPiece: newPiece, pieceImage: pieceImage };
  }

  render() {
    let renderBoard = this.handleBoardRender();

    return (
      <DndProvider backend={HTML5Backend}>
        <div className="board-section">
          <h1>Chess</h1>
          <div className="board">{renderBoard}</div>
        </div>
      </DndProvider>
    );
  }
}

Board.propTypes = {
  boardState: PropTypes.array.isRequired,
  whitePieces: PropTypes.array.isRequired,
  blackPieces: PropTypes.array.isRequired,
  toggleAvailableMovesBoard: PropTypes.array.isRequired,
  clickedPiecePosition: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  boardState: state.chess.boardState,
  whitePieces: state.chess.whitePieces,
  blackPieces: state.chess.blackPieces,
  toggleAvailableMovesBoard: state.chess.toggleAvailableMovesBoard,
  clickedPiecePosition: state.chess.clickedPiecePosition
});

export default connect(
  mapStateToProps,
  {
    updateBoard,
    updateBlackPieces,
    updateWhitePieces,
    toggleAvailableMoves
  }
)(Board);
