const PieceRules = {
  PawnRules(positionX, positionY, color, boardState) {
    let position = [];
    let direction = color === "black" ? 1 : -1;

    if (positionY < 7) {
      position.push([positionX, positionY + 1 * direction]);
    }
    if (
      (positionY === 1 && color === "black") ||
      (positionY === 6 && color === "white")
    ) {
      position.push([positionX, positionY + 2 * direction]);
    }
    return position;
  },

  KnightRules(positionX, positionY, color, boardState) {
    ///CAN BE OPTIMISED
    let position = [];

    if (
      positionY + 2 <= 7 &&
      positionX + 1 <= 7 &&
      (boardState[positionY + 2][positionX + 1].chessType === "" ||
        boardState[positionY + 2][positionX + 1].color !== color)
    ) {
      position.push([positionX + 1, positionY + 2]);
    }
    if (
      positionY - 2 >= 0 &&
      positionX + 1 <= 7 &&
      (boardState[positionY - 2][positionX + 1].chessType === "" ||
        boardState[positionY - 2][positionX + 1].color !== color)
    ) {
      position.push([positionX + 1, positionY - 2]);
    }
    if (
      positionY + 2 <= 7 &&
      positionX - 1 >= 0 &&
      (boardState[positionY + 2][positionX - 1].chessType === "" ||
        boardState[positionY + 2][positionX - 1].color !== color)
    ) {
      position.push([positionX - 1, positionY + 2]);
    }
    if (
      positionY - 2 >= 0 &&
      positionX - 1 >= 0 &&
      (boardState[positionY - 2][positionX - 1].chessType === "" ||
        boardState[positionY - 2][positionX - 1].color !== color)
    ) {
      position.push([positionX - 1, positionY - 2]);
    }
    if (
      positionY + 1 <= 7 &&
      positionX + 2 <= 7 &&
      (boardState[positionY + 1][positionX + 2].chessType === "" ||
        boardState[positionY + 1][positionX + 2].color !== color)
    ) {
      position.push([positionX + 2, positionY + 1]);
    }
    if (
      positionY - 1 >= 0 &&
      positionX + 2 <= 7 &&
      (boardState[positionY - 1][positionX + 2].chessType === "" ||
        boardState[positionY - 1][positionX + 2].color !== color)
    ) {
      position.push([positionX + 2, positionY - 1]);
    }
    if (
      positionY + 1 <= 7 &&
      positionX - 2 >= 0 &&
      (boardState[positionY + 1][positionX - 2].chessType === "" ||
        boardState[positionY + 1][positionX - 2].color !== color)
    ) {
      position.push([positionX - 2, positionY + 1]);
    }
    if (
      positionY - 1 >= 0 &&
      positionX - 2 >= 0 &&
      (boardState[positionY - 1][positionX - 2].chessType === "" ||
        boardState[positionY - 1][positionX - 2].color !== color)
    ) {
      position.push([positionX - 2, positionY - 1]);
    }
    return position;
  },
  RookRules(positionX, positionY, color, boardState) {
    let lookingForAvailableSquaresXPos = true;
    let lookingForAvailableSquaresYPos = true;
    let lookingForAvailableSquaresXNeg = true;
    let lookingForAvailableSquaresYNeg = true;
    let xAxisPos = 1;
    let xAxisNeg = -1;
    let yAxisPos = 1;
    let yAxisNeg = -1;
    let position = [];
    while (
      lookingForAvailableSquaresXPos ||
      lookingForAvailableSquaresXNeg ||
      lookingForAvailableSquaresYNeg ||
      lookingForAvailableSquaresYPos
    ) {
      if (lookingForAvailableSquaresXNeg && positionX + xAxisNeg >= 0) {
        if (
          boardState[positionY][positionX + xAxisNeg].chessType === "" ||
          boardState[positionY][positionX + xAxisNeg].color !== color
        ) {
          position.push([positionX + xAxisNeg, positionY]);
          if (boardState[positionY][positionX + xAxisNeg].chessType !== "") {
            lookingForAvailableSquaresXNeg = false;
          }
          xAxisNeg--;
        } else {
          lookingForAvailableSquaresXNeg = false;
        }
      } else {
        lookingForAvailableSquaresXNeg = false;
      }
      if (lookingForAvailableSquaresXPos && positionX + xAxisPos < 8) {
        if (
          boardState[positionY][positionX + xAxisPos].chessType === "" ||
          boardState[positionY][positionX + xAxisPos].color !== color
        ) {
          position.push([positionX + xAxisPos, positionY]);
          if (boardState[positionY][positionX + xAxisPos].chessType !== "") {
            lookingForAvailableSquaresXPos = false;
          }
          xAxisPos++;
        } else {
          lookingForAvailableSquaresXPos = false;
        }
      } else {
        lookingForAvailableSquaresXPos = false;
      }
      if (lookingForAvailableSquaresYNeg && positionY + yAxisNeg >= 0) {
        if (
          boardState[positionY + yAxisNeg][positionX].chessType === "" ||
          boardState[positionY + yAxisNeg][positionX].color !== color
        ) {
          position.push([positionX, positionY + yAxisNeg]);
          if (boardState[positionY + yAxisNeg][positionX].chessType !== "") {
            lookingForAvailableSquaresYNeg = false;
          }
          yAxisNeg--;
        } else {
          lookingForAvailableSquaresYNeg = false;
        }
      } else {
        lookingForAvailableSquaresYNeg = false;
      }

      if (lookingForAvailableSquaresYPos && positionY + yAxisPos < 8) {
        if (
          boardState[positionY + yAxisPos][positionX].chessType === "" ||
          boardState[positionY + yAxisPos][positionX].color !== color
        ) {
          position.push([positionX, positionY + yAxisPos]);
          if (boardState[positionY + yAxisPos][positionX].chessType !== "") {
            lookingForAvailableSquaresYPos = false;
          }
          yAxisPos++;
        } else {
          lookingForAvailableSquaresYPos = false;
        }
      } else {
        lookingForAvailableSquaresYPos = false;
      }
    }
    return position;
  },
  BishopRules(positionX, positionY, color, boardState) {
    let lookingForAvailableSquaresTopRight = true;
    let lookingForAvailableSquaresTopLeft = true;
    let lookingForAvailableSquaresBottomRight = true;
    let lookingForAvailableSquaresBottomLeft = true;
    let topRight = 1;
    let topLeft = 1;
    let bottomRight = 1;
    let bottomLeft = 1;
    let position = [];
    while (
      lookingForAvailableSquaresBottomLeft ||
      lookingForAvailableSquaresBottomRight ||
      lookingForAvailableSquaresTopLeft ||
      lookingForAvailableSquaresTopRight
    ) {
      if (
        lookingForAvailableSquaresTopRight &&
        positionX + topRight <= 7 &&
        positionY - topRight >= 0
      ) {
        if (
          boardState[positionY - topRight][positionX + topRight].chessType ===
            "" ||
          boardState[positionY - topRight][positionX + topRight].color !== color
        ) {
          position.push([positionX + topRight, positionY - topRight]);
          if (
            boardState[positionY - topRight][positionX + topRight].chessType !==
            ""
          ) {
            lookingForAvailableSquaresTopRight = false;
          }
          topRight++;
        } else {
          lookingForAvailableSquaresTopRight = false;
        }
      } else {
        lookingForAvailableSquaresTopRight = false;
      }
      if (
        lookingForAvailableSquaresTopLeft &&
        positionX - topLeft >= 0 &&
        positionY - topLeft >= 0
      ) {
        if (
          boardState[positionY - topLeft][positionX - topLeft].chessType ===
            "" ||
          boardState[positionY - topLeft][positionX - topLeft].color !== color
        ) {
          position.push([positionX - topLeft, positionY - topLeft]);
          if (
            boardState[positionY - topLeft][positionX - topLeft].chessType !==
            ""
          ) {
            lookingForAvailableSquaresTopLeft = false;
          }
          topLeft++;
        } else {
          lookingForAvailableSquaresTopLeft = false;
        }
      } else {
        lookingForAvailableSquaresTopLeft = false;
      }
      if (
        lookingForAvailableSquaresBottomRight &&
        positionX + bottomRight <= 7 &&
        positionY + bottomRight <= 7
      ) {
        if (
          boardState[positionY + bottomRight][positionX + bottomRight]
            .chessType === "" ||
          boardState[positionY + bottomRight][positionX + bottomRight].color !==
            color
        ) {
          position.push([positionX + bottomRight, positionY + bottomRight]);
          if (
            boardState[positionY + bottomRight][positionX + bottomRight]
              .chessType !== ""
          ) {
            lookingForAvailableSquaresBottomRight = false;
          }
          bottomRight++;
        } else {
          lookingForAvailableSquaresBottomRight = false;
        }
      } else {
        lookingForAvailableSquaresBottomRight = false;
      }
      if (
        lookingForAvailableSquaresBottomLeft &&
        positionX - bottomLeft >= 0 &&
        positionY + bottomLeft <= 7
      ) {
        if (
          boardState[positionY + bottomLeft][positionX - bottomLeft]
            .chessType === "" ||
          boardState[positionY + bottomLeft][positionX - bottomLeft].color !==
            color
        ) {
          position.push([positionX - bottomLeft, positionY + bottomLeft]);
          if (
            boardState[positionY + bottomLeft][positionX - bottomLeft]
              .chessType !== ""
          ) {
            lookingForAvailableSquaresBottomLeft = false;
          }
          bottomLeft++;
        } else {
          lookingForAvailableSquaresBottomLeft = false;
        }
      } else {
        lookingForAvailableSquaresBottomLeft = false;
      }
    }
    return position;
  },
  QueenRules(positionX, positionY, color, boardState) {
    let position = [];
    let lookingForAvailableSquaresXPos = true;
    let lookingForAvailableSquaresYPos = true;
    let lookingForAvailableSquaresXNeg = true;
    let lookingForAvailableSquaresYNeg = true;
    let xAxisPos = 1;
    let xAxisNeg = -1;
    let yAxisPos = 1;
    let yAxisNeg = -1;
    while (
      lookingForAvailableSquaresXPos ||
      lookingForAvailableSquaresXNeg ||
      lookingForAvailableSquaresYNeg ||
      lookingForAvailableSquaresYPos
    ) {
      if (lookingForAvailableSquaresXNeg && positionX + xAxisNeg >= 0) {
        if (
          boardState[positionY][positionX + xAxisNeg].chessType === "" ||
          boardState[positionY][positionX + xAxisNeg].color !== color
        ) {
          position.push([positionX + xAxisNeg, positionY]);
          if (boardState[positionY][positionX + xAxisNeg].chessType !== "") {
            lookingForAvailableSquaresXNeg = false;
          }
          xAxisNeg--;
        } else {
          lookingForAvailableSquaresXNeg = false;
        }
      } else {
        lookingForAvailableSquaresXNeg = false;
      }
      if (lookingForAvailableSquaresXPos && positionX + xAxisPos < 8) {
        if (
          boardState[positionY][positionX + xAxisPos].chessType === "" ||
          boardState[positionY][positionX + xAxisPos].color !== color
        ) {
          position.push([positionX + xAxisPos, positionY]);
          if (boardState[positionY][positionX + xAxisPos].chessType !== "") {
            lookingForAvailableSquaresXPos = false;
          }
          xAxisPos++;
        } else {
          lookingForAvailableSquaresXPos = false;
        }
      } else {
        lookingForAvailableSquaresXPos = false;
      }
      if (lookingForAvailableSquaresYNeg && positionY + yAxisNeg >= 0) {
        if (
          boardState[positionY + yAxisNeg][positionX].chessType === "" ||
          boardState[positionY + yAxisNeg][positionX].color !== color
        ) {
          position.push([positionX, positionY + yAxisNeg]);
          if (boardState[positionY + yAxisNeg][positionX].chessType !== "") {
            lookingForAvailableSquaresYNeg = false;
          }
          yAxisNeg--;
        } else {
          lookingForAvailableSquaresYNeg = false;
        }
      } else {
        lookingForAvailableSquaresYNeg = false;
      }

      if (lookingForAvailableSquaresYPos && positionY + yAxisPos < 8) {
        if (
          boardState[positionY + yAxisPos][positionX].chessType === "" ||
          boardState[positionY + yAxisPos][positionX].color !== color
        ) {
          position.push([positionX, positionY + yAxisPos]);
          if (boardState[positionY + yAxisPos][positionX].chessType !== "") {
            lookingForAvailableSquaresYPos = false;
          }
          yAxisPos++;
        } else {
          lookingForAvailableSquaresYPos = false;
        }
      } else {
        lookingForAvailableSquaresYPos = false;
      }
    }
    let lookingForAvailableSquaresTopRight = true;
    let lookingForAvailableSquaresTopLeft = true;
    let lookingForAvailableSquaresBottomRight = true;
    let lookingForAvailableSquaresBottomLeft = true;
    let topRight = 1;
    let topLeft = 1;
    let bottomRight = 1;
    let bottomLeft = 1;
    while (
      lookingForAvailableSquaresBottomLeft ||
      lookingForAvailableSquaresBottomRight ||
      lookingForAvailableSquaresTopLeft ||
      lookingForAvailableSquaresTopRight
    ) {
      if (
        lookingForAvailableSquaresTopRight &&
        positionX + topRight <= 7 &&
        positionY - topRight >= 0
      ) {
        if (
          boardState[positionY - topRight][positionX + topRight].chessType ===
            "" ||
          boardState[positionY - topRight][positionX + topRight].color !== color
        ) {
          position.push([positionX + topRight, positionY - topRight]);
          if (
            boardState[positionY - topRight][positionX + topRight].chessType !==
            ""
          ) {
            lookingForAvailableSquaresTopRight = false;
          }
          topRight++;
        } else {
          lookingForAvailableSquaresTopRight = false;
        }
      } else {
        lookingForAvailableSquaresTopRight = false;
      }
      if (
        lookingForAvailableSquaresTopLeft &&
        positionX - topLeft >= 0 &&
        positionY - topLeft >= 0
      ) {
        if (
          boardState[positionY - topLeft][positionX - topLeft].chessType ===
            "" ||
          boardState[positionY - topLeft][positionX - topLeft].color !== color
        ) {
          position.push([positionX - topLeft, positionY - topLeft]);
          if (
            boardState[positionY - topLeft][positionX - topLeft].chessType !==
            ""
          ) {
            lookingForAvailableSquaresTopLeft = false;
          }
          topLeft++;
        } else {
          lookingForAvailableSquaresTopLeft = false;
        }
      } else {
        lookingForAvailableSquaresTopLeft = false;
      }
      if (
        lookingForAvailableSquaresBottomRight &&
        positionX + bottomRight <= 7 &&
        positionY + bottomRight <= 7
      ) {
        if (
          boardState[positionY + bottomRight][positionX + bottomRight]
            .chessType === "" ||
          boardState[positionY + bottomRight][positionX + bottomRight].color !==
            color
        ) {
          position.push([positionX + bottomRight, positionY + bottomRight]);
          if (
            boardState[positionY + bottomRight][positionX + bottomRight]
              .chessType !== ""
          ) {
            lookingForAvailableSquaresBottomRight = false;
          }
          bottomRight++;
        } else {
          lookingForAvailableSquaresBottomRight = false;
        }
      } else {
        lookingForAvailableSquaresBottomRight = false;
      }
      if (
        lookingForAvailableSquaresBottomLeft &&
        positionX - bottomLeft >= 0 &&
        positionY + bottomLeft <= 7
      ) {
        if (
          boardState[positionY + bottomLeft][positionX - bottomLeft]
            .chessType === "" ||
          boardState[positionY + bottomLeft][positionX - bottomLeft].color !==
            color
        ) {
          position.push([positionX - bottomLeft, positionY + bottomLeft]);
          if (
            boardState[positionY + bottomLeft][positionX - bottomLeft]
              .chessType !== ""
          ) {
            lookingForAvailableSquaresBottomLeft = false;
          }
          bottomLeft++;
        } else {
          lookingForAvailableSquaresBottomLeft = false;
        }
      } else {
        lookingForAvailableSquaresBottomLeft = false;
      }
    }
    return position;
  },
  KingRules(positionX, positionY, color, boardState) {
    let position = [];
    if (
      positionX + 1 <= 7 &&
      positionY + 1 <= 7 &&
      (boardState[positionY + 1][positionX + 1].chessType === "" ||
        boardState[positionY + 1][positionX + 1].color !== color)
    ) {
      position.push([positionX + 1, positionY + 1]);
    }
    if (
      positionX + 1 <= 7 &&
      (boardState[positionY][positionX + 1].chessType === "" ||
        boardState[positionY][positionX + 1].color !== color)
    ) {
      position.push([positionX + 1, positionY]);
    }
    if (
      positionX + 1 <= 7 &&
      positionY - 1 >= 0 &&
      (boardState[positionY - 1][positionX + 1].chessType === "" ||
        boardState[positionY - 1][positionX + 1].color !== color)
    ) {
      position.push([positionX + 1, positionY - 1]);
    }
    if (
      positionX - 1 >= 0 &&
      positionY + 1 <= 7 &&
      (boardState[positionY + 1][positionX - 1].chessType === "" ||
        boardState[positionY + 1][positionX - 1].color !== color)
    ) {
      position.push([positionX - 1, positionY + 1]);
    }
    if (
      positionX - 1 >= 0 &&
      (boardState[positionY][positionX - 1].chessType === "" ||
        boardState[positionY][positionX - 1].color !== color)
    ) {
      position.push([positionX - 1, positionY]);
    }
    if (
      positionX - 1 >= 0 &&
      positionY - 1 >= 0 &&
      (boardState[positionY - 1][positionX - 1].chessType === "" ||
        boardState[positionY - 1][positionX - 1].color !== color)
    ) {
      position.push([positionX - 1, positionY - 1]);
    }
    if (
      positionY + 1 <= 7 &&
      (boardState[positionY + 1][positionX].chessType === "" ||
        boardState[positionY + 1][positionX].color !== color)
    ) {
      position.push([positionX, positionY + 1]);
    }
    if (
      positionY - 1 >= 0 &&
      (boardState[positionY - 1][positionX].chessType === "" ||
        boardState[positionY - 1][positionX].color !== color)
    ) {
      position.push([positionX, positionY - 1]);
    }
    return position;
  }
};

export default PieceRules;
