import React from "react";
import { useDrop } from "react-dnd";
import BoardPiece from "./BoardPiece";
import { TYPES } from "./Piece";

import PropTypes from "prop-types";
import { connect } from "react-redux";
import { updateBoard } from "../actions/chessActions.js";

function SquarePiece({
  divstyle,
  piece,
  imagestyle,
  pieceimageindex,
  thisX,
  thisY,
  thisBoardState,
  availableMoves,
  clickedPiecePosition,
  updateBoard
}) {
  const [{ isOver, canDrop }, drop] = useDrop({
    accept: [
      TYPES.KNIGHT,
      TYPES.PAWN,
      TYPES.QUEEN,
      TYPES.ROOK,
      TYPES.KING,
      TYPES.BISHOP
    ],
    //candrop checks whatever is being dragged and highlights this square yellow if can move here
    canDrop: () => {
      if (availableMoves[0]) {
        for (let i = 0; i < availableMoves[0].length; i++) {
          if (
            availableMoves[0][i][0] === thisX &&
            availableMoves[0][i][1] === thisY
          ) {
            return true;
          }
        }
        return false;
      }
    },
    drop: () => {
      //here you update board
      let currentBoardState = [...thisBoardState];
      for (let y = 0; y < currentBoardState.length; y++) {
        for (let x = 0; x < currentBoardState[y].length; x++) {
          if (y === thisY && x === thisX) {
            currentBoardState[y][x] = clickedPiecePosition.piece;
          }
          if (
            y === clickedPiecePosition.position[1] &&
            x === clickedPiecePosition.position[0]
          ) {
            currentBoardState[y][x] = { piece: "", color: undefined };
          }
        }
      }
      updateBoard(currentBoardState);
    },
    collect: monitor => ({
      isOver: monitor.isOver(),
      canDrop: monitor.canDrop()
    })
  });
  return (
    <div style={divstyle} ref={drop}>
      {piece.chessType !== "" && (
        <BoardPiece
          imagestyle={imagestyle}
          piece={piece}
          pieceimageindex={pieceimageindex}
          thisX={thisX}
          thisY={thisY}
          thisBoardState={thisBoardState}
        />
      )}
      {!isOver && canDrop && <Overlay color="yellow" />}
      {isOver && canDrop && <Overlay color="green" />}
    </div>
  );
}

function Overlay({ color }) {
  return (
    <div
      style={{
        position: "absolute",
        top: 0,
        left: 0,
        height: "100%",
        width: "100%",
        zIndex: 1,
        opacity: 0.5,
        backgroundColor: color
      }}
    />
  );
}

SquarePiece.propTyes = {
  updateBoard: PropTypes.func.isRequired
};

export default connect(
  null,
  { updateBoard }
)(SquarePiece);
